.movie {
  height: 350px;
  width: 300px;
  border-radius: 6px;
  background-size: cover;
  overflow: hidden;
  position: relative;
  border-width: 4px;
  border-style: solid;
  margin: auto;
  border-color: #ccc;
  background-color: #ccc;
}

.movie a {
  text-decoration: none;
}

.movie .image {
  height: 50%;
  position: relative;
}

.movie .image img {
  max-width: 100%;
}

.movie .image .overlay_movie {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 35%;
}

.movie .movie_rating {
  position: absolute;
  top: 37%;
  left: 2%;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #081c22;
  color: #fff;
  padding: 5px;
  display: grid;
  place-items: center;
}

.movie .movie_rating .CircularProgressbar .CircularProgressbar-text {
  font-size: 2rem;
  font-weight: bold;
}

.movie .movie_rating .progress_rating .inside_progress {
  position: absolute;
  top: -2px;
  left: -2px;
  width: 49px;
  height: 50px;
  left: -2px;
  border-radius: 50%;
  border: 4px solid #21d07a;
}

.movie .movie_release_data {
  position: absolute;
  top: 47%;
  left: 27%;
  color: #fff;
  text-transform: capitalize;
  font-size: 0.9rem;
}

.movie .info {
  height: 44%;
  border-width: 0 5px;
  border-style: solid;
  border-color: transparent;
  position: relative;
  top: 6%;
  color: #fff;
  padding: 0 10px;
}

.movie .info h3 {
  margin: 0 0 10px 0;
}

.movie .info p {
  margin: 0;
  font-size: 15px;
}
